<template>
  <transition name="slide-fade">
    <div class="norms" v-show="show">
      <div class="mark" @click="hide"></div>
      <div class="yaa">
        <div class="yaa-head">
          <div class="yaa-img">
            <img :src="obj.imgurl" alt />
          </div>
          <div class="yaa-titel">
            <p v-text="obj.name">惠普小印手持照片打印机 Sprocket PLUS</p>
            <p>白色</p>
            <p>
              <span>￥</span>
              <span v-text="obj.newprice?obj.newprice+'.00':obj.oldprice+'.00'"></span>
            </p>
          </div>
        </div>
        <div class="yaa-color">
          <p>颜色选择</p>
          <div>
            <span>白色</span>
          </div>
        </div>
        <div class="yaa-num">
          <p>选择数量</p>
          <button id="remove" @click="remove">-</button>
          <span v-text="count"></span>
          <button id="add" @click="add(obj.sum)">+</button>
        </div>
        <div class="yaa-sure">
          <span>
            <van-icon name="bag-o" />
          </span>
          <input type="button" value="确定" @click="sure(obj.id)" />
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  data() {
    return {
      count: 1
    };
  },
  props: ["show", "obj", "router"],
  methods: {
    hide() {
      return this.$emit("sure", false);
    },
    add(sum) {
      if (this.count < sum) {
        this.count++;
      }
    },
    remove() {
      if (this.count > 1) {
        this.count--;
      }
    },
    sure(id) {
      this.$store.commit("setBuy", {
        id,
        num: this.count
      });
      window.console.log(this.$store.getters.getRouter);
      if (this.$store.getters.getRouter == "shoppcar") {
        this.id = this.$store.getters.getId;
        this.num = this.$store.getters.getBuy.num;
        this.$axios
          .get(`http://localhost:3000/addcar?id=${this.id}&&num=${this.num}`)
          .then(() => {
            this.$router.push({ name: this.$store.getters.getRouter });
          });
      } else {
        this.$router.push({ name: this.$store.getters.getRouter });
      }
    }
  }
};
</script>
<style lang="scss" scoped>
@function vw($px) {
  @return ($px/375) * 100vw;
}
.slide-fade-enter-active {
  transition: all 0.4s ease;
}
.slide-fade-enter,
.slide-fade-leave-to {
  opacity: 0;
}
.norms {
  position: fixed;
  top: 0;
  width: 100%;
  height: vw(850);
  z-index: 100;
  .mark {
    height: vw(850);
    background-color: rgba(0, 0, 0, 0.8);
    opacity: 1;
  }
  .yaa {
    position: fixed;
    bottom: 0;
    background: #fff;
    z-index: 666;
    width: 100%;
    height: vw(385);
    .yaa-head {
      width: 100%;
      height: vw(112);
      box-shadow: 0 5px 13px rgba(0, 0, 0, 0.12);
      background: #f7f7f7;
      background-image: linear-gradient(#fefefe, #f6f6f6);
      .yaa-img {
        position: absolute;
        top: vw(-30);
        left: vw(15);
        border-radius: vw(8);
        overflow: hidden;
        img {
          width: vw(110);
          height: vw(110);
        }
      }
      .yaa-titel {
        width: vw(222);
        margin: vw(17);
        margin-top: vw(12);
        float: right;
        font-size: vw(14);
        p:nth-child(1) {
          height: vw(20);
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }
        p:nth-child(2) {
          margin-top: vw(5);
          font-size: vw(12);
          color: #666;
        }
        p:nth-child(3) {
          margin-top: vw(15);
          color: #d44;
          font-weight: 550;
          font-size: vw(16);
          span:nth-child(1) {
            font-size: vw(12);
          }
        }
      }
    }
    .yaa-color {
      margin-top: vw(5);
      p {
        padding: 0 vw(13);
        display: block;
        font-size: vw(15);
        color: #666;
        font-weight: 550;
      }
      div {
        width: vw(375);
        height: vw(70);
        margin-top: vw(10);
        padding: vw(10) vw(12) vw(6);
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
        span {
          width: vw(29);
          display: block;
          border: 1px solid #698fe7;
          padding: vw(10);
          font-size: vw(14);
          border-radius: vw(5);
          margin: vw(15);
        }
      }
    }
    .yaa-num {
      margin-top: vw(5);
      border-bottom: 1px solid #ccc;
      p {
        padding: 0 vw(13);
        display: block;
        font-size: vw(15);
        color: #666;
        font-weight: 550;
        padding-bottom: vw(8);
        border-bottom: 1px solid #ccc;
      }
      button {
        margin: vw(15);
        width: vw(28);
        height: vw(28);
        border-radius: 50%;
        border: none;
        outline: none;
        text-align: center;
        background: #fff;
        border: 1px solid #ccc;
      }
    }
    .yaa-sure {
      span {
        display: inline-block;
        margin: vw(10) 0 0 vw(20);
        font-size: vw(20);
      }
      input {
        width: vw(290);
        height: vw(40);
        background: #d44;
        border: none;
        vertical-align: top;
        margin-top: vw(5);
        border-radius: vw(5);
        margin-left: vw(35);
        color: #fff;
      }
    }
  }
}
</style>