<template>
  <div id="details" @touchmove="scroll">
    <header>
      <input type="button" value="返回" id="gohome" @click="tohome" />
      <p v-text="obj.name"></p>
    </header>
    <ul class="nav">
      <li :class="{active:tag==0?1:0}" @click="pages(0)">商品</li>
      <li :class="{active:tag==1?1:0}" @click="pages(1)">详情</li>
      <li :class="{active:tag==2?1:0}" @click="pages(2)">参数</li>
      <li :class="{active:tag==3?1:0}" @click="pages(3)">推荐</li>
    </ul>
    <div class="img">
      <img :src="obj.imgurl" alt />
    </div>
    <div class="loding" v-show="lodingshow==1?1:0">
      <van-loading color="#6666" />
    </div>
    <div class="title" v-show="lodingshow==0?1:0">
      <p v-text="obj.name"></p>
      <p>公益宝贝</p>
      <p>
        <span>￥</span>
        <span v-text="obj.newprice==0?obj.oldprice+'.00':obj.newprice+'.00'"></span>
        <span>满 99 元包邮</span>
      </p>
      <p>优惠信息</p>
      <p>
        <span>领劵</span>
        <span>10元锤子商城通用优惠券</span>
        <span>1 个优惠券</span>
      </p>
    </div>
    <div class="title-wrap">
      <p>
        全款预定说明
        <span>
          <van-icon name="arrow" />
        </span>
      </p>
    </div>
    <Spdetails />
    <Parameter />
    <Recommend />
    <Norms :show="show" @sure="sure" :obj="obj" />
    <div class="foot">
      <van-icon name="bag-o" />
      <span @click="showyaa">现在购买</span>
      <span @click="tocar">加入购物车</span>
      <span v-text="num" @click="car"></span>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import { Icon, Sku, Loading } from "vant";
import Spdetails from "../components/Spdetails";
import Parameter from "../components/Parameter";
import Recommend from "../components/Recommend";
import Norms from "../components/Norms";
Vue.use(Icon);
Vue.use(Sku);
Vue.use(Loading);
export default {
  data() {
    return {
      id: 0,
      obj: {},
      tag: 0,
      show: false,
      router: "",
      num: 0,
      lodingshow: 1
    };
  },
  created() {
    this.id = this.$store.getters.getId;
    this.$axios
      .get(`http://localhost:3000/details?id=${this.id}`)
      .then(data => {
        this.obj = data.data;
        this.lodingshow = 0;
      });
    this.$axios.get(`http://localhost:3000/car`).then(data => {
      this.num = data.data.length;
    });
  },
  deactivated() {
    //离开销毁组件避免缓存
    this.$destroy("Details");
  },
  methods: {
    tohome() {
      this.$router.push("/");
    },
    pages(page) {
      this.tag = page;
      if (page == 0) {
        window.scrollTo(0, 0);
      } else if (page == 1) {
        window.scrollTo(0, 590);
      } else if (page == 2) {
        window.scrollTo(0, 3160);
      } else if (page == 3) {
        window.scrollTo(0, 4515);
      }
    },
    scroll() {
      if (scrollY < 570) {
        this.tag = 0;
      }
      if (scrollY >= 570) {
        this.tag = 1;
      }
      if (scrollY >= 3100) {
        this.tag = 2;
      }
      if (scrollY >= 4500) {
        this.tag = 3;
      }
    },
    showyaa() {
      this.$store.commit("setRouter", "buy");
      this.show = true;
    },
    tocar() {
      this.$store.commit("setRouter", "shoppcar");
      this.show = true;
    },
    sure(b) {
      this.show = b;
    },
    car() {
      this.$router.push({ name: "shoppcar" });
    }
  },
  components: {
    Spdetails,
    Parameter,
    Recommend,
    Norms
  }
};
</script>
<style lang="scss" scoped>
@function vw($px) {
  @return ($px/375) * 100vw;
}
.loding {
  width: 100%;
  height: vw(160);
  padding-top: vw(70);
  background: #fff;
  margin-top: vw(10);
  text-align: center;
}
#details {
  background: #e7e2e2;
  header {
    width: vw(375);
    height: vw(50);
    text-align: center;
    line-height: vw(50);
    font-size: vw(18);
    background: #000;
    color: #fff;
    font-weight: 600;
    position: fixed;
    top: 0;
    #gohome {
      border: none;
      background: #111010;
      border: 1px solid rgb(48, 45, 45);
      color: #fff;
      float: left;
      width: vw(54);
      height: vw(32);
      font-size: vw(14);
      line-height: vw(32);
      border-radius: vw(8);
      margin: vw(8) 0 0 vw(8);
    }
    p {
      white-space: nowrap;
      overflow: hidden;
      width: vw(250);
      text-overflow: ellipsis;
      margin-left: vw(80);
    }
  }
  .nav {
    list-style: none;
    padding: vw(10);
    background: #f2f2f2;
    box-shadow: 0 5px 13px rgba(0, 0, 0, 0.12);
    display: flex;
    position: fixed;
    top: vw(50);
    z-index: 1;
    li {
      padding: vw(3) vw(29);
      border: vw(1) solid rgb(223, 221, 221);
      color: #666;
      font-weight: 600;
      font-size: vw(14);
    }
    li:nth-child(1) {
      border-radius: vw(5) 0 0 vw(5);
    }
    li:last-child {
      border-radius: 0 vw(5) vw(5) 0;
    }
    .active {
      background: #bfbfbf;
      background-image: linear-gradient(#aeaeae, #b3b3b3);
      box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.15);
      color: #fff;
    }
  }
  .img {
    margin-top: vw(100);
    width: vw(375);
