        margin-left: vw(20);
      }
      span:nth-child(3) {
        color: #d44d44;
        font-size: vw(14);
        float: right;
        margin: vw(3) vw(5) 0 0;
      }
    }
  }
  .foot {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: vw(60);
    background: #fff;
    line-height: vw(60);
    font-size: vw(25);
    padding-left: vw(20);
    color: #666;
    i {
      vertical-align: middle;
    }
    span:nth-child(2) {
      display: inline-block;
      width: vw(150);
      height: vw(40);
      font-size: vw(16);
      color: #fff;
      background: linear-gradient(#e96e65, #df4b43);
      border-radius: vw(10);
