    }
  }
  .title-wrap {
    width: 100%;
    height: vw(40);
    box-sizing: border-box;
    padding: 0 vw(12);
    line-height: vw(40);
    background: #fff;
    margin-top: vw(8);
    font-size: vw(15);
    font-weight: 600;
    color: #666;
    span {
      float: right;
      vertical-align: middle;
    }
  }
}
</style>