<template>
  <div id="login">
    <header>
      账号密码登录
      <span @click="tozhuce">注册</span>
    </header>
    <div class="home">
      <span>国家和地区</span>
      <span>中国</span>
    </div>
    <div class="tel">
      <label for="#tel">
        <span>+86</span>
      </label>
      <input type="text" placeholder="手机号" id="tel" v-model="tel" />
    </div>
    <div class="pas">
      <input type="password" placeholder="密码" id="pas" v-model="pas" />
      <span v-if="error">账号密码错误</span>
    </div>
    <input type="button" id="btnlogin" value="登录" @click="login" :style="{background:color}" />
    <p class="icon">
      <i class="weui-icon-success weui-icon_msg"></i>
      <span>自动登录</span>
      <span>短信验证码登录</span>
      <span>忘记密码</span>
    </p>
    <p class="foot">
      <span></span>
      <span></span>
    </p>
    <p class="foot-text">
      <span>登录代表你已同意 &nbsp;</span>
      <span>法律声明 &nbsp;</span>
      <span>和 &nbsp;</span>
      <span>隐私条款</span>
    </p>
  </div>
</template>
<script>
export default {
  data() {
    return {
      pas: "",
      tel: "",
      error: 0,
      color: "#6788d088"
    };
  },
  watch: {
    pas() {
      if (this.tel.trim() && this.pas.trim()) {
        this.color = "#6788d0";
      } else {
        this.color = "#6788d088";
      }
    }
  },
  methods: {
    login() {
      if (this.tel.trim() && this.pas.trim()) {
        let pas = this.$store.getters.getLoginpas;
        let tel = this.$store.getters.getLogin;
        if (tel == this.tel && this.pas == pas) {
          this.error = 0;
          this.$router.push({ path: "/Main/Person" });
          this.pas = "";
          this.tel = "";
        } else {
          this.error = 1;
        }
      }
    },
    tozhuce() {
      this.tel = "";
      this.pas = "";
      this.$router.push({ name: "zhuce" });
    }
  }
};
</script>
<style lang="scss" scoped>
@function vw($px) {
  @return ($px/375) * 100vw;
}
#login {
  height: vw(850);
  background: #e5e5e588;
  position: relative;
  header {
    height: vw(50);
    background: #000;
    font-size: vw(18);
    color: #fff;
    text-align: center;
    line-height: vw(50);
    span {
      height: vw(30);
      line-height: vw(30);
      position: absolute;
      right: vw(10);
      top: vw(10);
      font-size: vw(14);
      padding: 0 vw(10);
      background: rgba(204, 49, 28, 0.9);
      border-radius: vw(10);
      font-weight: 600;
    }
  }
  .home {
    width: 85%;
    height: vw(40);
    margin: vw(10) auto;
    border: 1px solid #ccc;
    background: #fff;
    border-radius: 4px;
    box-shadow: 0 3px 5px -4px rgba(0, 0, 0, 0.4) inset,
      -1px 0 3px -2px rgba(0, 0, 0, 0.1) inset;
    line-height: vw(40);
    span:nth-child(1) {
      margin-left: vw(5);
      font-size: vw(14);
      color: #666;
    }
    span:nth-child(2) {
      float: right;
      margin-right: vw(10);
      font-size: vw(14);
      color: #666;
    }
  }
  .tel {
    width: 85%;
    height: vw(40);
    margin: auto;
    background: #fff;
    border-radius: 4px 4px 0 0;
    box-shadow: 0 3px 5px -4px rgba(0, 0, 0, 0.4) inset,
      -1px 0 3px -2px rgba(0, 0, 0, 0.1) inset;
    #tel {
      outline: none;
      border: none;
      font-size: vw(12);
      margin-left: vw(8);
    }
    label {
      font-size: vw(12);
      span:nth-child(1) {
        margin-left: vw(8);
        line-height: vw(40);
      }
    }
  }
  .pas {
    width: 85%;
    height: vw(40);
    margin: auto;
    background: #fff;
    border-top: 1px solid #ccc5;
    border-radius: 0 0 4px 4px;
    position: relative;
    #pas {
      outline: none;
      border: none;
      font-size: vw(12);
      margin-left: vw(8);
      line-height: vw(40);
    }
    span {
      position: absolute;
      right: vw(110);
      top: vw(40);
      font-size: vw(14);
      color: red;
    }
  }
  #btnlogin {
    display: block;
    width: 85%;
    height: vw(50);
    background: #6788d088;
    outline: none;
    border: none;
    border-radius: vw(10);
    color: #fff;
    margin: vw(20) auto 0;
  }
  .icon {
    width: 85%;
    display: block;
    margin: vw(10) auto;
    font-size: vw(12);
    color: #666;
    .weui-icon-success {
      width: vw(20);
      height: vw(20);
      color: #6788d0;
    }
    span:nth-child(2) {
      vertical-align: middle;
      margin-left: vw(5);
    }
    span:nth-child(3) {
      vertical-align: middle;
      margin-left: vw(80);
    }
    span:nth-child(4) {
      vertical-align: middle;
      margin-left: vw(25);
    }
  }
  .foot {
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: vw(40);
    span:nth-child(1) {
      display: inline-block;
      background: url(../assets/footlogo.png) no-repeat -59px 0;
      background-size: auto vw(40);
      width: vw(42);
      height: vw(40);
      margin: 0 vw(5);
    }
    span:nth-child(2) {
      display: inline-block;
      background: url(../assets/footlogo.png) no-repeat -119px 0;
      background-size: auto vw(40);
      width: vw(42);
      height: vw(40);
      margin: 0 vw(5);
    }
  }
  .foot-text {
    width: 100%;
    position: absolute;
    bottom: vw(15);
    text-align: center;
    color: #666;
    font-size: vw(12);
    span:nth-child(2) {
      color: #6788d0;
    }
    span:nth-child(4) {
      color: #6788d0;
    }
  }
}
</style>