<template>
  <div>
    <form action="/">
      <van-search
        v-model="value"
        placeholder="请输入搜索关键词"
        show-action
        @search="onSearch"
        @cancel="onCancel"
      />
    </form>
    <router-view></router-view>
  </div>
</template>
<script>
import Vue from "vue";
import { Search } from "vant";

Vue.use(Search);
export default {
  data() {
    return {
      value: ""
    };
  },
  methods: {
    onSearch() {
      this.$store.commit("setValue", {
        value: this.value
      });
      this.$router.push({ path: "/Searlist" });
      this.value = "";
    },
    onCancel() {
      this.$router.push({ path: "/" });
    }
  }
};
</script>
<style lang="scss" scoped>
@function vw($px) {
  @return ($px/375) * 100vw;
}
.van-search__content {
  width: vw(287);
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  padding-left: 8px;
  background-color: #f7f8fa;
  border-radius: vw(15);
}
</style>