                <img src="../assets/wx.png" alt />
              </div>
              <p>微信支付</p>
            </div>
            <div class="weui-cell__ft">
              <input type="radio" class="weui-check" name="radio1" id="x11" />
              <span class="weui-icon-checked"></span>
            </div>
          </label>
          <label class="weui-cell weui-check__label" for="x12">
            <div class="weui-cell__bd">
              <div class="img">
                <img src="../assets/zfb.png" alt />
              </div>

              <p>支付宝</p>
            </div>
            <div class="weui-cell__ft">
              <input type="radio" name="radio1" class="weui-check" id="x12" checked="checked" />
              <span class="weui-icon-checked"></span>
            </div>
          </label>
          <label class="weui-cell weui-check__label" for="x13">
            <div class="weui-cell__bd">
              <div class="img">
                <img src="../assets/huabei.png" alt />
              </div>
              <p>蚂蚁花呗分期</p>
            </div>
            <div class="weui-cell__ft">
              <input type="radio" name="radio1" class="weui-check" id="x13" checked="checked" />
              <span class="weui-icon-checked"></span>
            </div>
          </label>
        </div>
      </div>
    </div>
    <div class="sp">
      <p>
        商品信息
        <span>件商品</span>
        <span v-text="num"></span>
        <span>总计</span>
      </p>
      <div class="list">
        <img :src="obj.imgurl" alt />
        <div>
          <p>
            <span v-text="obj.name">畅呼吸电子温湿度计</span>
            <span>（白色）</span>
          </p>
          <p>
            <span>￥</span>
            <span v-text="obj.newprice?obj.newprice+'.00':obj.oldprice+'.00'">0.00</span>
            <span>x</span>
            <span v-text="num"></span>
          </p>
        </div>
      </div>
    </div>
    <div class="bill">
      <div class="weui-cell weui-cell_access">
        <div class="weui-cell__bd">
          <span style="vertical-align: middle">发票信息</span>
        </div>
        <div class="weui-cell__ft"></div>
      </div>
      <div class="weui-cell weui-cell_access">
        <div class="weui-cell__bd">
          <span style="vertical-align: middle">优惠券</span>
        </div>
        <div class="weui-cell__ft"></div>
      </div>
    </div>
    <div class="money">
      <div class="weui-cell weui-cell_access">
        <div class="weui-cell__bd">
          <span style="vertical-align: middle">商品金额</span>
        </div>
      </div>
      <div class="weui-cell weui-cell_access">
        <div class="weui-cell__bd">
          <span style="vertical-align: middle">商品总计</span>
        </div>
        <span>￥</span>
        <span v-text="allmoney"></span>
      </div>
      <div class="weui-cell weui-cell_access">
        <div class="weui-cell__bd">
          <span style="vertical-align: middle">运费</span>
        </div>
        <span>￥0.00</span>
      </div>
      <div class="weui-cell weui-cell_access">
        <div class="weui-cell__bd">
          <span style="vertical-align: middle">应付金额</span>
        </div>
        <span style="color:#d44;font-weight:600;font-size:12px;">￥</span>
        <span style="color:#d44;font-weight:600;font-size:16px;" v-text="allmoney">0.00</span>
      </div>
    </div>
    <div class="allmoney">
      <div class="weui-cell weui-cell_access">
        <span>应付金额</span>
        <span>￥</span>
        <span v-text="allmoney">0.00</span>
        <span class="paybtn">现在支付</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      obj: {},
      id: 0,
      num: 0
    };
  },
  methods: {
    todeatils() {
      this.$router.push({ name: "details" });
    },
    tosite() {
      this.$router.push({ name: "site" });
    }
  },
  created() {
    this.id = this.$store.getters.getBuy.id;
    this.num = this.$store.getters.getBuy.num;
    this.$axios
      .get(`http://localhost:3000/details?id=${this.id}`)
      .then(data => {
        this.obj = data.data;
      });
  },
  deactivated() {
    //离开销毁组件避免缓存
    this.$destroy("Buy");
  },
  computed: {
    allmoney() {
      let price = 0;
      if (this.obj.nweprice) {
        price = this.obj.nweprice * this.num;
      } else {
        price = this.obj.oldprice * this.num;
      }
      return price.toFixed(2);
    }
  }
};
</script>
