<template>
  <van-search placeholder="请输入搜索关键词" v-model="value" @click="tosearch" />
</template>
<script>
import Vue from "vue";
import { Search } from "vant";

Vue.use(Search);
export default {
  data() {
    return {
      value: ""
    };
  },
  methods: {
    tosearch() {
      this.$router.push({ name: "search" });
    }
  }
};
</script>
<style lang="scss" scoped>
@function vw($px) {
  @return ($px/375) * 100vw;
}
.van-search__content {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  background-color: #fff;
  border-radius: 2px;
  padding: vw(8);
  .van-cell {
    flex: 1;
    padding: 6px 10px;
    border-radius: vw(18);
    background-color: transparent;
    background: #f5f5f5;
  }
}
</style>