<template>
  <div>
    <form action="/">
      <van-search
        v-model="value"
        placeholder="请输入搜索关键词"
        show-action
        @search="onSearch"
        @cancel="onCancel"
      />
    </form>
    <ul id="searlist">
      <li v-for="(item,index) in list" :key="index" :id="item.id" class="tx">
        <div class="el-left">
          <img :src="item.imgurl" alt />
        </div>
        <div class="el-right">
          <p v-text="item.name"></p>
          <p>
            <span>8G + 128GB</span>
            <span>8G + 256GB</span>
            <span>12G + 256GB</span>
          </p>
          <p>
            <span v-text="item.newprice==0?'':item.oldprice"></span>
            <span>￥</span>
            <span v-text="item.newprice==0?item.oldprice:item.newprice"></span>
          </p>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import Vue from "vue";
import { Search } from "vant";

Vue.use(Search);
export default {
  data() {
    return {
      value: "",
      list: []
    };
  },
  created() {
    this.value = this.$store.getters.getValue;
    this.$axios
      .get(
        `http://localhost:3000/searlist?name=${this.$store.getters.getValue}`
      )
      .then(data => {
        this.list = data.data;
      });
  },
  deactivated() {
    this.$destroy("Searchlist");
  },
  methods: {
    onSearch() {
      this.$store.commit("setValue", {
        value: this.value
      });
      this.$axios
        .get(
          `http://localhost:3000/searlist?name=${this.$store.getters.getValue}`
        )
        .then(data => {
          this.list = data.data;
        });
    },
    onCancel() {
      this.$router.push({ path: "/" });
    }
  }
};
</script>
<style lang="scss" scoped>
@function vw($px) {
  @return ($px/375) * 100vw;
}
.van-search__content {
  width: vw(287);
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  padding-left: 8px;
  background-color: #f7f8fa;
  border-radius: vw(15);
}
.tx:after {
  content: "";
  clear: both;
  height: 0px;
  display: block;
  overflow: hidden;
  visibility: hidden;
}
#searlist {
  list-style: none;
  li {
    padding: vw(12);
    .el-left {
      float: left;
      img {
        width: vw(126);
        height: vw(126);
      }
    }
    .el-right {
      float: left;
      margin-left: vw(20);
      p:nth-child(1) {
        display: block;
        max-width: vw(200);
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-top: vw(20);
        font-weight: 600;
        font-size: vw(14);
      }
      p:nth-child(2) {
        margin-top: vw(10);
        font-weight: 600;
        font-size: vw(8);
        span {
          border: 1px solid #94b3f7;
          background: #e5efff;
          margin-right: vw(5);
          color: blue;
          padding: 0 vw(5);
        }
      }
      p:nth-child(3) {
        margin-top: vw(20);
        font-weight: 600;
        font-size: vw(14);
        span {
          color: #d44d44;
        }
        span:nth-child(1) {
          color: #ccc;
          text-decoration: line-through;
          font-size: vw(10);
        }
      }
    }
  }
}
</style>