<template>
  <div class="car">
    <header>
      购物车
      <span v-if="delshow" @click="show">编辑</span>
      <span v-if="delshow==0?1:0" @click="show">完成</span>
    </header>
    <component :is="Car" :delshow="delshow" />
  </div>
</template>
<script>
import Car from "../components/Car.vue";
export default {
  data() {
    return {
      Car: "Car",
      delshow: 1
    };
  },
  methods: {
    show() {
      this.delshow = !this.delshow;
    }
  },
  components: {
    Car
  },
  deactivated() {
    //离开销毁组件避免缓存
    this.$destroy("Shoppcar");
    window.console.log(1);
  }
};
</script>
<style lang="scss" scoped>
@function vw($px) {
  @return ($px/375) * 100vw;
}
.car {
  background: #ededed;
  overflow: hidden;
  header {
    width: vw(375);
    height: vw(50);
    text-align: center;
    line-height: vw(50);
    font-size: vw(18);
    background: #000;
    color: #fff;
    font-weight: 600;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    span {
      position: absolute;
      right: vw(8);
      width: vw(50);
      height: vw(40);
      color: #fff;
      font-size: vw(14);
    }
  }
}
</style>