<template>
  <van-list
    v-model="loading"
    :finished="finished"
    finished-text="没有更多了"
    @load="onLoad"
    :immediate-check="false"
    :offset="10"
  >
    <div class="list-top">
      <p>猜你喜欢</p>
    </div>
    <div class="list-item">
      <ul>
        <li v-for="(item,index) in list" :key="index" :id="item.id" @click="todateils(item.id)">
          <div>
            <img :src="item.imgurl" alt />
          </div>
          <p v-text="item.name"></p>
          <p>
            <span>￥</span>
            <span v-text="item.newprice==0?item.oldprice.toFixed(2):item.newprice.toFixed(2)"></span>
            <span v-text="item.newprice==0?'':item.oldprice.toFixed(2)"></span>
            <span>新品</span>
          </p>
        </li>
      </ul>
    </div>
  </van-list>
</template>
<script>
import Vue from "vue";
import { List } from "vant";

Vue.use(List);
export default {
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      num: 0
    };
  },

  methods: {
    onLoad() {
      this.num++;
      // 异步更新数据
      setTimeout(() => {
        this.$axios
          .get(`http://localhost:3000/homeadd?index=${this.num}`)
          .then(data => {
            this.list.push(...data.data[0].list);
          });
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.num >= 3) {
          this.finished = true;
        }
      }, 1000);
    },
    todateils(id) {
      this.$store.commit("setId", {
        id
      });
      this.$router.push({ name: "details" });
    }
  },
  created() {
    this.$axios.get("http://localhost:3000/homelist").then(data => {
      this.list = data.data[0].list;
    });
  }
};
</script>
<style lang="scss" scoped>
@function vw($px) {
  @return ($px/375) * 100vw;
}
.list-top {
  width: 80%;
  margin: vw(50) auto 0;
  border-top: 1px dotted #e9e9e9;
  padding: 0 0 vw(15) vw(20);
  p {
    font-weight: 700;
    color: rgba(0, 0, 0, 0.8);
    font-size: vw(17);
    margin: vw(15) 0 0 0;
  }
}
.list-item {
  width: 95%;
  margin: auto;
  ul {
    padding-bottom: 20px;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    li {
      width: 48%;
      margin-top: vw(15);
      div {
        background: rgba(0, 0, 0, 0.03);
        border-radius: vw(10);
        img {
          width: vw(170);
          height: vw(170);
          mix-blend-mode: multiply;
        }
      }
      p:nth-child(2) {
        font-size: vw(14);
        font-weight: 600;
        height: vw(20);
        overflow: hidden;
        margin-left: vw(5);
      }
      p:nth-child(3) {
        span:nth-child(1) {
          font-size: vw(10);
          color: #d44d44;
          font-weight: 600;
        }
        span:nth-child(2) {
          font-size: vw(14);
          color: #d44d44;
          font-weight: 600;
        }
        span:nth-child(3) {
          font-size: vw(12);
          color: #ccc;
          font-weight: 500;
          margin-left: vw(5);
          text-decoration: line-through;
        }
        span:nth-child(4) {
          float: right;
          font-size: vw(10);
          color: #d44d44;
          font-weight: 600;
          padding: 0 vw(8);
          border-radius: vw(7);
          background: #f6ebea;
          position: relative;
          top: vw(3);
        }
      }
    }
  }
}
</style>
