<template>
  <div>
    <Search />
    <div class="nav-box">
      <div class="nav-tag">
        <ul>
          <li
            v-for="(item,index) in taglist"
            :key="index"
            :class="{active:index==tag?1:0}"
            v-text="item.name"
            @click="pages(index)"
          >手机数码</li>
        </ul>
      </div>
      <div class="nav-list">
        <div v-for="(item,index) in navlist" :key="index" class="list-box tx">
          <p v-text="item.classifyName"></p>
          <div class="list" v-for="(list,index) in item.third" :key="index">
            <div>
              <img :src="list.image" alt />
            </div>
            <span v-text="list.classifyName">坚果 Pro3</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Search from "../components/Search";
export default {
  components: {
    Search
  },
  data() {
    return {
      taglist: [
        {
          name: "手机数码"
        },
        {
          name: "官方配件"
        },
        {
          name: "鞋包服饰"
        },
        {
          name: "畅呼吸"
        },
        {
          name: "医药保健"
        },
        {
          name: "电脑办公"
        }
      ],
      tag: 0,
      navlist: []
    };
  },
  methods: {
    pages(index) {
      this.tag = index;
      this.$axios
        .get(`http://localhost:3000/navlist?index=${this.tag}`)
        .then(data => {
          this.navlist = data.data.list;
        });
    }
  },
  created() {
    this.$axios
      .get(`http://localhost:3000/navlist?index=${this.tag}`)
      .then(data => {
        this.navlist = data.data.list;
      });
  }
};
</script>
<style lang="scss" scoped>
@function vw($px) {
  @return ($px/375) * 100vw;
}
.tx::after {
  content: "";
  clear: both;
  height: 0px;
  display: block;
  overflow: hidden;
  visibility: hidden;
}
.nav-box {
  height: vw(705);
  width: 100%;
  border-top: vw(1) solid #ccc;
  position: relative;
  .nav-tag {
    width: 25%;
    height: 100%;
    border-right: vw(1) solid #ccc;
    overflow: hidden;
    ul {
      list-style: none;
      li {
        margin: vw(20) auto;
        width: 80%;
        padding: vw(3) vw(5);
        font-size: vw(14);
        font-weight: 600;
        text-align: center;
        border-radius: vw(5);
        color: #666;
      }
      .active {
        color: #d44d44;
        background: #f6ebea;
      }
    }
  }
  .nav-list {
    width: 72%;
    position: absolute;
    top: 0;
    right: 0;
    .list-box {
      p {
        font-size: vw(18);
        font-weight: 550;
        margin: vw(15) 0 0 vw(5);
      }
      .list {
        width: vw(85);
        margin: vw(10) vw(5) 0 0;
        float: left;
        div {
          background: rgba(0, 0, 0, 0.03);
          border-radius: vw(5);
          img {
            width: vw(85);
            height: vw(85);
            mix-blend-mode: multiply;
          }
        }
        span {
          font-size: vw(12);
          display: block;
          text-align: center;
          width: 100%;
          font-weight: 550;
          margin-top: vw(2);
        }
      }
    }
  }
}
</style>