import Vuex from "vuex";
import Vue from "vue";
Vue.use(Vuex);
export default new Vuex.Store({
    state: {
        login: {
            name: '',
            pas: ''
        },
        search: {
            value: '',
        },
        details: {
            id: '',
        },
        buy: {
            id: '',
            num: '',
        },
        router: '',
        car: {
            num: ''
        }
    },
    mutations: {
        setLogin(state, newval) {
            state.login = newval;
        },
        setValue(state, newval) {
            state.search = newval;
        },
        setId(state, id) {
            state.details = id;
        },
        setRouter(state, newval) {
            state.router = newval;
        },
        setBuy(state, newval) {
            state.buy = newval;
        },
        setCar(state, newval) {
            state.car = newval;
        }
    },
    getters: {
        getLogin(state) {
            return state.login.name
        },
        getLoginpas(state) {
            return state.login.pas
        },
        getValue(state) {
            return state.search.value
        },
        getId(state) {
            return state.details.id
        },
        getRouter(state) {
            return state.router
        },
        getBuy(state) {
            return state.buy
        },
        getCar(state) {
            return state.car
        }
    }
})