<template>
  <div @touchmove="scroll">
    <header>
      <img src="../assets/logo.png" alt />
    </header>
    <Search />
    <Swipe />
    <Homebox />
    <Homelist />
    <span class="totop" @click="totop" v-show="show">
      <img src="../assets/timg.gif" alt />
    </span>
  </div>
</template>
<script>
import Search from "../components/Search";
import Swipe from "../components/swipe";
import Homebox from "../components/Homebox";
import Homelist from "../components/Homelist";
export default {
  data() {
    return {
      show: 0
    };
  },
  components: {
    Search,
    Swipe,
    Homebox,
    Homelist
  },
  methods: {
    totop() {
      window.scrollTo(0, 0);
      this.show = 0;
    },
    scroll() {
      if (scrollY >= 2000) {
        this.show = 1;
      } else {
        this.show = 0;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@function vw($px) {
  @return ($px/375) * 100vw;
}
* {
  margin: 0;
  padding: 0;
}
header {
  height: vw(35);
  font-size: 0;
  img {
    width: vw(18);
    height: vw(18);
    display: block;
    margin: vw(10) auto;
  }
}
.totop {
  position: fixed;
  bottom: vw(60);
  right: vw(5);
  img {
    width: vw(60);
    height: vw(60);
  }
}
</style>