<template>
  <div>
    <Loding v-if="show==2?1:0" />
    <Koncar v-if="show==0?1:0" />
    <div class="spcar" v-if="show==1?1:0">
      <header>
        <i></i>
        <span>点击领取优惠券</span>
        <i></i>
      </header>
      <div class="car-list">
        <div class="top">
          <span>预</span>
          <span>发货日期 : 预售商品预计将于 2019.11.05 陆续发货</span>
        </div>
        <div class="list" v-for="(item,index) in arr" :key="index">
          <ul>
            <li>
              <span
                class="check active"
                @click="check(index)"
                :id="item.id"
                :in="index"
                :num="item.num"
                :money="item.newprice==0?item.oldprice:item.newprice"
              ></span>
              <img :src="item.imgurl" alt />
              <div>
                <p>预售</p>
                <p v-text="item.name"></p>
                <span>白色</span>
                <p>
                  <span>￥</span>
                  <span v-text="item.newprice==0?item.oldprice+'.00':item.newprice+'.00'"></span>
                  <span>x</span>
                  <span v-text="item.num"></span>
                </p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <Recommend />
      <div class="bottom">
        <span class="checkall active" @click="checkall"></span>
        <span>已选</span>
        <span v-text="numall"></span>
        <span>件</span>
        <span v-show="delshow">合计：</span>
        <span v-show="delshow">￥</span>
        <span v-text="moneyall" v-show="delshow"></span>
        <input type="button" v-show="delshow" value="现在结算" />
        <input type="button" v-show="delshow==0?1:0" @click="delall" value="删除所选" />
      </div>
    </div>
  </div>
</template>

<script>
import Recommend from "../components/Recommend";
import Koncar from "../components/Koncar";
import Loding from "../components/Loding";
export default {
  data() {
    return {
      arr: [],
      num: 0,
      show: 2,
      index: [],
      id: [],
      numall: 0,
      moneyall: 0
    };
  },
  props: ["delshow"],
  components: {
    Recommend,
    Koncar,
    Loding
  },
  created() {
    let money = 0;
    this.$axios.get(`http://localhost:3000/car`).then(data => {
      this.arr = data.data;
      if (data.data.length == 0) {
        this.show = 0;
      } else {
        this.show = 1;
        for (let i = 0; i < data.data.length; i++) {
          let a = 0;
          this.numall += data.data[i].num;
          if (data.data[i].newprice != 0) {
            a = data.data[i].newprice * data.data[i].num * 1;
            money += a;
          } else {
            a = data.data[i].oldprice * data.data[i].num * 1;
            money += a;
          }
        }
        this.moneyall = money.toFixed(2);
      }
    });
  },
  methods: {
    check(index) {
      let checknum = 0;
      let newid = [];
      let newindex = [];
      let check = document.getElementsByClassName("check");
      let checkall = document.getElementsByClassName("checkall")[0];
      if (check[index].className == "check active") {
        check[index].className = "check";
      } else {
        check[index].className = "check active";
      }
      for (let i = 0; i < check.length; i++) {
        if (check[i].className == "check active") {
          checknum++;
          newid.push(check[i].id);
          newindex.push(check[i].getAttribute("in"));
        }
      }
      if (checknum == check.length) {
        checkall.className = "checkall active";
      } else {
        checkall.className = "checkall";
      }
      this.id = newid;
      this.index = newindex;
    },
    checkall() {
      let checkall = document.getElementsByClassName("checkall")[0];
      let check = document.getElementsByClassName("check");
      if (checkall.className == "checkall active") {
        checkall.className = "checkall";
        for (let i = 0; i < check.length; i++) {
          check[i].className = "check";
        }
        this.moneyall = 0;
        this.numall = 0;
      } else {
        checkall.className = "checkall active";
        for (let i = 0; i < check.length; i++) {
          check[i].className = "check active";
        }
        this.moneyall = 0;
        this.numall = 0;
        for (let i = 0; i < check.length; i++) {
          if (check[i].className == "check active") {
            this.numall += check[i].getAttribute("num") * 1;
            this.moneyall +=
              check[i].getAttribute("num") *
              1 *
              check[i].getAttribute("money") *
              1;
          }
        }
        this.moneyall = this.moneyall.toFixed(2);
      }
    },
    delall() {
      this.$axios.get(`http://localhost:3000/delcar?id=${this.id}`).then(() => {
        this.$axios.get(`http://localhost:3000/car`).then(data => {
          this.arr = data.data;
          if (data.data.length == 0) {
            this.show = 0;
          } else {
            this.show = 1;
          }
          let check = document.getElementsByClassName("check");
          for (let i = 0; i < check.length; i++) {
            check[i].className = "check";
            this.numall = 0;
            this.moneyall = 0;
          }
        });
        this.$forceUpdate();
      });
    }
  },
  watch: {
    index() {
      this.moneyall = 0;
      this.numall = 0;
      let check = document.getElementsByClassName("check");
      for (let i = 0; i < check.length; i++) {
        if (check[i].className == "check active") {
          this.numall += check[i].getAttribute("num") * 1;
          this.moneyall +=
            check[i].getAttribute("num") *
            1 *
            check[i].getAttribute("money") *
            1;
        }
      }
      this.moneyall = this.moneyall.toFixed(2);
    },
    delshow() {
      let check = document.getElementsByClassName("check");
      let checkall = document.getElementsByClassName("checkall")[0];
      if (!this.delshow) {
        checkall.className = "checkall";
        for (let i = 0; i < check.length; i++) {
          check[i].className = "check";
          this.numall = 0;
          this.moneyall = 0;
        }
      }
    }
  }
};
</script>
<style lang="scss" scoped>
