        background: rgba(0, 0, 0, 0.05);
        transform-origin: 0 100%;
      }
    }
    .list {
      ul {
        list-style: none;
        li {
          width: 100%;
          height: vw(114);
          .check {
            position: relative;
            top: vw(-30);
