            background: url(../assets/checkbox.png) no-repeat;
            background-size: 100% auto;
            background-position: 0 -80px;
          }
          .active {
            background-position: 0 0;
          }
          img {
            width: vw(90);
            height: vw(90);
            margin: vw(12) 0 0 vw(25);
          }
          div {
            margin-top: vw(5);
            float: right;
            font-size: vw(12);
            p:nth-child(1) {
              color: #cc3e39;
              background: #ffe7e5;
              border-color: #f0948d;
              font-size: vw(8);
              display: inline-block;
