              border: 1px solid rgba(212, 77, 68, 0.3);
            }
            p:nth-child(2) {
              height: vw(28);
              font-weight: 600;
              width: vw(208);
              margin-top: vw(5);
              line-height: vw(14);
            }
            span:nth-child(3) {
              color: #777;
              font-size: vw(11);
            }
            p:nth-child(4) {
              span:nth-child(1) {
                font-size: vw(10);
                font-weight: 600;
                color: #cc3e39;
              }
              span:nth-child(2) {
                font-size: vw(12);
                font-weight: 600;
                color: #cc3e39;
              }
              span:nth-child(3) {
                font-size: vw(12);
                font-weight: 600;
                color: rgb(158, 147, 147);
                margin-left: vw(5);
              }
              span:nth-child(4) {
                font-size: vw(12);
                font-weight: 600;
                color: rgb(158, 147, 147);
                margin-left: vw(5);
              }
            }
          }
        }
      }
    }
  }
  .bottom {
    position: fixed;
    bottom: vw(50);
    z-index: 90;
    background: #fafafa;
    box-shadow: 0 -0.2rem 1rem rgba(0, 0, 0, 0.1);
    font-size: 12px;
    color: #666;
    width: 100%;
    padding: 0 0 0 vw(12);
    box-sizing: border-box;
    height: vw(54);
    line-height: vw(54);
    .checkall {
      position: relative;
      display: inline-block;
      width: 23px;
