      background: url(../assets/checkbox.png) no-repeat;
      background-size: 100% auto;
      background-position: 0 -80px;
      vertical-align: middle;
    }
    .active {
      background-position: 0 0;
    }
    span:nth-child(2) {
      margin-left: vw(10);
    }
    span:nth-child(3) {
      font-size: vw(14);
      font-weight: 600;
      margin: 0 vw(5);
    }
    span:nth-child(5) {
      font-size: vw(12);
      color: #000;
      margin-left: vw(55);
    }
    span:nth-child(6) {
      font-size: vw(10);
      color: #cc3e39;
      font-weight: 600;
    }
    span:nth-child(7) {
      color: #cc3e39;
