    }
    input {
      position: absolute;
      top: vw(5);
      right: vw(6);
      height: 40px;
      line-height: 40px;
      border-radius: 6px;
      text-align: center;
      text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.15);
      color: #fff;
      background: linear-gradient(#6f94e8, #5178df);
      border: none;
      width: vw(110);
      font-weight: 600;
