import VueRouter from "vue-router";
import Vue from "vue";
Vue.use(VueRouter);
import Main from "../pages/Main.vue"
import Home from "../pages/Home.vue"
import Nav from "../pages/Nav.vue"
import Sear from "../pages/Sear.vue"
import Shoppcar from '../pages/Shoppcar.vue'
import Seartext from "../components/Searchtext.vue"
import Searlist from "../components/Searchlist.vue"
import Person from "../pages/Personal.vue"
import Login from "../pages/Login.vue"
import Zhuce from "../pages/zhuce.vue"
import Details from "../pages/Details.vue"
import Buy from '../pages/Buy.vue'
import Site from '../pages/Site.vue'
const routes = [
    {
        path: '/Main',
        component: Main,
        name: 'main',
        children: [{
            path: 'Home',
            component: Home,
            name: 'home'
        }, {
            path: 'Person',
            component: Person,
            name: 'person'
        }, {
            path: 'Nav',
            component: Nav,
            name: 'nav'
        }, {
            path: 'Shoppcar',
            component: Shoppcar,
            name: 'shoppcar',
        }]
    },
    {
        path: '/Search',
        component: Sear,
        name: 'search',
        redirect: '/Search/Seartext',
        children: [{
            path: 'Seartext',
            component: Seartext,
            name: 'seartext'
        }],

    }, {
        path: '/Searlist',
        component: Searlist,
        name: 'searlist'
    }, {
        path: '/Login',
        component: Login,
        name: 'login'
    },
    {
        path: '/Zhuce',
        component: Zhuce,
        name: 'zhuce'
    },
    {
        path: '/Details',
        component: Details,
        name: 'details'
    },
    {
        path: '/Buy',
        component: Buy,
        name: 'buy'
    },
    {
        path: '/Site',
        component: Site,
        name: 'site'
    },
    {
        path: '/',
        redirect: '/Main/Home'
    },
]

const router = new VueRouter({
    routes,
})
router.beforeEach((to, from, next) => {
    window.scrollTo(0, 0);
    next();
})
export default router